import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { useTranslation } from 'react-i18next';

import styles from './StatsItem.module.scss';
import commonStyles from '../../common.module.scss';

const StatsItem = (props) => {
  const { img, title, value, className } = props;
  const { t } = useTranslation();
  return (
    <div className={classNames(styles.StatsItem, className)}>
      <div className={styles.statsIconWrap}>
        <img src={img} alt="icon" />
      </div>
      <div className={styles.statsTextWrap}>
        <h2>{t(`${title}`)}</h2>
        <p>{value}</p>
      </div>
    </div>
  );
};

StatsItem.propTypes = {
  className: PropTypes.string,
  img: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
};

StatsItem.defaultProps = {
  className: '',
};

export default StatsItem;
