import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import numeral from 'numeral';

import styles from './FlipClockCard.module.scss';

const FlipClockCard = (props) => {
  const { currTime, title } = props;
  const [prevTime, setPrevTime] = useState(currTime);
  const [flipped, setFlipped] = useState(false);

  useEffect(() => {
    if (currTime !== prevTime) {
      setFlipped(true);
    }
  }, [currTime, prevTime]);

  const reset = () => {
    setFlipped(false);
    setPrevTime(currTime);
  };

  return (
    <div className={styles.FlipClockCard}>
      <div className={styles.card}>
        <div className={styles.background}>{numeral(currTime).format('00')}</div>
        <div className={styles.bottom}>{numeral(prevTime).format('00')}</div>
        <div
          className={classNames(styles.flipper, { [styles.flipped]: flipped })}
          onTransitionEnd={reset}>
          <div className={styles.front}>{numeral(prevTime).format('00')}</div>
          <div className={styles.back}>{numeral(currTime).format('00')}</div>
        </div>
        <hr className={styles.divider} />
      </div>
      {!!title && <h2 className={styles.title}>{title}</h2>}
    </div>
  );
};

FlipClockCard.propTypes = {
  currTime: PropTypes.number.isRequired,
  title: PropTypes.string,
};

FlipClockCard.defaultProps = {
  title: '',
};

export default FlipClockCard;
