import { useState, useEffect } from 'react';
import { PHASE_1_NFT_AMOUNT } from '../constants/misc';
import { useZepochNodeContract } from './useContract';
import { useIsMounted } from './useIsMounted';
import { useUpdate } from './useUpdate';

export const useCurrentPhase = () => {
  const [phase, setPhase] = useState(0);
  const { updateByTimer } = useUpdate();
  const zepochNodeContract = useZepochNodeContract();
  const isMounted = useIsMounted();

  useEffect(() => {
    const fetch = async () => {
      try {
        if (!zepochNodeContract) {
          return;
        }
        const zepochNodeTotalSupply = (await zepochNodeContract.totalSupply()).toNumber();
        if (!isMounted) {
          return;
        }
        if (zepochNodeTotalSupply < PHASE_1_NFT_AMOUNT) {
          setPhase(1);
          return;
        }
        setPhase(2);
      } catch (error) {
        console.log(error);
      }
    };
    fetch();
  }, [updateByTimer, zepochNodeContract, isMounted]);

  return phase;
};
